*:not(.slick-track):not(.slick-list) {
  transition: .25s ease;
}

body {
  font-family: "Gotham Pro", sans-serif;
}

img {
  width: 100%;
}

a {
  border-bottom: unset;
  
  &:hover {
    text-decoration: none;
  }
}

.footer .container {    
  max-width: 1504px;
  padding: 0 48px;
  margin: 0 auto;

  @media(max-width: 959px) {
    padding: 0 32px;
  }

  @media(max-width: 767px) {
    padding: 0 16px;
  }
}

.footer__nav {
  a {
    color: inherit;
    
  }
}

.footer__cont-link {
  font-weight: 100;
}

.footer__menu a {
  font-weight: 100;
  border-bottom: 1px solid transparent;

  &:hover {
    color: #00adef;
    border-bottom-color: #00adef;
  }
}

.footer__btm {
  font-weight: 100;
}

.main {
  margin-top: 75px;
  padding-bottom: 100px;

  min-height: calc(100vh - 145px);
  display: grid;
  overflow: hidden;

  @include media-breakpoint-down(md) {
    min-height: unset;
    overflow: auto;
  }

  @include media-breakpoint-down(sm) {
    margin-top: 50px;
    padding-bottom: 50px;
  }
}

// typography

.h1,
.h2,
.h3,
.h4,
.b1,
.b2,
.b3 {
  font-weight: 300;
}

.h5,
.h6,
.h7 {
  font-weight: 400;
}

.h1 {
  font-size: 48px;
}

.h2 {
  font-size: 31px;
}

.h3 {
  font-size: 27px;
}

.h4 {
  font-size: 19px;
}

.h5 {
  font-size: 15px;
  line-height: 18px;
}

.h6 {
  font-size: 13px;
}

.h7 {
  font-size: 11px;
}

.b1 {
  font-size: 15px;
}

.b2 {
  font-size: 13px;
}

.b3 {
  font-size: 11px;
}

h1.page-title,
h2.page-title {
  font-size: 31px;
  line-height: 37px;
  margin-bottom: 25px;

  @media(max-width: 1023px) {
    font-size: 31px;
    line-height: 37px;
    margin-bottom: 25px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}

h1.page-title,
h2.page-title {
  text-align: left;
  max-width: none;
  margin: 0 0 30px;
  color: #444;
  
  @media(max-width: 767px) {
    margin-bottom: 15px;

    &.center {
      text-align: center;
    }
  }
}

main.main.dashboard .page-title_redirect .page-title__slider {
  color: #212529;
  transition: .25s ease;

  &:hover {
    color: #888;

    .direct-marker_light {
      opacity: 1;
      margin-left: 10px;
    }

    .direct-marker {
      margin-left: 10px;
    }
  }
}

main.main.common {
  padding-top: 50px;
  margin: 0;
}

//
.btn {
  background: none;
  border: none;
  outline: transparent;

  &:active,
  &:focus,
  &:focus-visible {
    outline: transparent;
  }
}

.cut-in-mobile {
  display: contents;

  @include media-breakpoint-down(sm) {
    display: none;
  }
}

.section-title {
  margin-bottom: 25px;
  line-height: 37px;

  @include media-breakpoint-down(sm) {
    font-size: 19px;
    line-height: 23px;
  }
}

.section {
  padding: 75px 0 0;

  &:first-child {
    padding: 50px 0 75px;
  }

  &:last-child {
    padding-bottom: 0;
  }

  @include media-breakpoint-down(sm) {
    padding: 50px 0 0;

    &:first-child {
      padding: 25px 0 0;
    }
  }
}

.slick-slide {
  outline: transparent;

  &:active,
  &:focus {
    outline: transparent;
  }
}

