body {
  .datepickers-container {
    @include media-breakpoint-down(sm) {
      position: static !important;
    }
  }

  .datepicker {
    @include media-breakpoint-down(sm) {
      transition: unset, opacity 0.25s ease !important;
      left: 50% !important;
      top: 150% !important;
      transform: translate(-50%, -50%) !important;
      visibility: hidden;
      position: fixed !important;
    }

    .datepicker--buttons {
      .datepicker--button {
        display: none !important;
      }

      .c-btn-datepicker {
        padding: 15px;
        height: auto;
        border: 1px solid #EEEEEE;
        font-size: 15px;
        line-height: 18px;
        color: #444444;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        margin: 0 10px;
        border-radius: 5px;

        &.disabled {
          opacity: .7;
          cursor: not-allowed;
          pointer-events: none;
        }

        &:hover {
          padding: 15px;
          height: auto;
          border: 1px solid #EEEEEE;
          background: #fff;
        }
      }
    }
  }

  .datepicker.active {
    @include media-breakpoint-down(sm) {
      left: 50% !important;
      top: 50% !important;
      transform: translate(-50%, -50%);
      visibility: visible;
    }
  }

  .datepicker--pointer {
    @include media-breakpoint-down(sm) {
      display: none !important;
    }
  }

  .datepicker--nav-title {
    color: $black;

    i {
      color: $black;
    }
  }

  .datepicker--day-name {
    color: $black;
  }

  .datepicker--cell.-current- {
    color: $white;
    background: $primary;

    &:hover {
      color: $white;
    }
  }

  .datepicker--cell.datepicker--cell-day.-current- {
    background: #eee;
    color: #000;
  }
}

.mob-datePicker-bg {
  opacity: .25;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #000;
  z-index: 99;
}


.span-file {
  padding: 12px 15px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px #eee dashed;
  border-radius: 5px;
  overflow: hidden;
  outline: transparent;
  color: #444;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: border-color .4s ease;
  -o-transition: border-color .4s ease;
  transition: border-color .4s ease;
  font-size: 16px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  white-space: normal;
  line-height: 20px;

  @include media-breakpoint-down(sm) {
    font-size: 13px;
    text-align: center;
  }
}

.span-file.loaded {
  border: 1px #eee solid;
}

.input-container input:hover + .span-file.loaded {
  border: 1px $primary solid;
}

.input-container input:hover + .span-file {
  border-color: $primary;
}

.remove-file {
  display: none;
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url(/images/icons/exit-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: $bg;
  background-size: 10px;
  cursor: pointer;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
  right: 17px;

  &:hover {
    background-color: $stroke;
  }
}

.file-input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.input-container.input-container-file {
  top: 28px;

  .input-wrp {
    height: auto;
  }
}


.popup-content {
  padding: 50px 30px;
  overflow-y: visible;

  @include media-breakpoint-down(sm) {
    //max-height: calc(100vh - 286px);
    overflow-y: scroll;
  }

  .tabs-container {
    margin: 0;
  }

  .tabs-content {
    margin-top: 30px;
  }

  .tabs-control-box {
    flex-wrap: nowrap;
    overflow: auto;

    @include media-breakpoint-down(sm) {
      justify-content: flex-start;
      margin-right: -30px;
    }
  }


  .tab-item {
    width: auto;
  }

}

.popup-content.popup-content_info {
  padding: 30px;
}

.modal-form {
  margin-top: 30px;
}

.alert-window-text {
  margin-top: 10px;
}

.modal-title {
  margin-bottom: 15px;
  line-height: 37px;

  @include media-breakpoint-down(sm) {
    font-size: 19px;
    line-height: 23px;
  }
}

.modal-text-block {
  line-height: 23px;
  color: $minor;

  @include media-breakpoint-down(sm) {
    font-size: 13px;
    line-height: 19px;
  }
}

.modal-controls {
  margin-top: 50px;
  display: flex;
  justify-content: center;

  .button {
    @include media-breakpoint-down(sm) {
      width: 100%;
      max-width: 100%;
    }
  }

  @include media-breakpoint-down(sm) {
    padding: 15px 30px;
    margin: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    flex-direction: column;
    background: #fff;
  }
}

.popup-sms-phone {
  color: #444;
  font-weight: 500;
}

.popup {
  .button {
    width: 250px;
    max-width: 100%;

    @include media-breakpoint-down(sm) {
      width: 100%;
    }
  }
}

.button-modal-help {
  margin-right: 15px;

  @include media-breakpoint-down(sm) {
    margin: 0 0 15px 0;
  }
}

.service-programs-popup.popup {

  .popup-content-c {
    width: auto;
    max-width: unset;
    padding: 30px 30px 70px;
  }

  .slick-dots {
    position: absolute;
    display: flex;
    padding: 0;
    margin: 0;
    top: -36px;
    width: calc(100% - 45px);
    list-style: none;

    li {
      width: 100%;
      outline: none;
      height: 4px;

      button {
        width: 100%;
        border: unset;
        color: transparent;
        background: #F6F6F6;
        height: 4px;
        overflow: hidden;
        outline: none;
      }
    }

    .slick-active {
      button {
        background: #DADADA;
      }
    }
  }
}

.popup-decorative-picture {
  width: 100%;
  height: 180px;
  background-color: red;

  @include media-breakpoint-down(sm) {
    height: 160px;
    border-radius: 0;
    background-size: cover;
  }
}

.code-disable-timer {
  padding-left: 12px;
}

.sbr-tabs {
  max-width: 100vw;
  overflow: hidden;
}

article.select-device-wrp {
  width: 835px;
  max-width: 100%;

  &.popup .popup-content-c {
    width: 100%;
    max-width: 100%;
  }

  .modal-text-block {
    margin-bottom: 30px;
  }

  @media(max-width: 767px) {
    .popup-content-c,
    .popup-content {
      height: 100%;
    }

    .popup-content-c {
      padding-bottom: 100px;
    }

    .popup-content {
      overflow-y: auto;
      padding-bottom: 0;
    }
  }

  @media(max-width: 500px) {
    .select-device .option-item,
    .select-device .option-item:last-child {
      width: 100%;
      max-width: 100%;
      margin-right: 0;
    }
  }
}

.select-device-wrp .select-device {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-right: -15px;

  .select {
    height: unset;
    background: #E8F8FE !important;
  }

  .option-item {
    display: flex;
    width: calc(50% - 7px);
    max-width: calc(50% - 7px);
    margin-bottom: 15px;

    &:last-child {
      margin-right: 15px;
    }

    &:nth-child(even) {
      margin-right: 0;
    }

    @include media-breakpoint-down(sm) {
      width: calc(50% - 8px);
      max-width: calc(50% - 8px);
      flex-direction: column;
      margin-right: 15px;
    }

    @include media-breakpoint-down(xs) {
      padding: 15px;
    }
  }

  .switch-box {
    width: 100%;
  }

  .product-item-pic {
    @include media-breakpoint-down(sm) {
      width: 150px;
      max-width: 100%;
      height: 90px;
    }
  }

  .product-item-info {
    overflow: hidden;
  }

  .product-item-data-value {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.pick-up-point {
  .option-item {
    width: 100%;
    margin-right: 0;
    flex-basis: unset;
    margin-bottom: 15px;
  }

  .option-title {
    color: #888;
  }

  .select {
    .option-title {
      color: $black;
    }
  }
}

.title-description {
  padding: 30px;
  background-repeat: no-repeat;

  @include media-breakpoint-down(sm) {
    background-position: right;
  }

  h3 {
    font-size: 27px;
    line-height: 33px;
    font-weight: 300;
    margin-bottom: 15px;

    @include media-breakpoint-down(sm) {
      font-size: 19px;
      line-height: 23px;
    }
  }

  p {
    font-weight: 300;
    font-size: 15px;
    line-height: 23px;
    margin-bottom: 20px;

    @include media-breakpoint-down(sm) {
      font-size: 13px;
      line-height: 19px;
    }
  }
}

.list {
  margin: 0;
  padding: 0;
}

.list__elem {
  list-style: none;
  position: relative;
  padding-left: 23px;
  font-weight: 300;
  font-size: 15px;
  line-height: 23px;
  color: #888888;

  &:before {
    content: '';
    display: block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #00ADEF;
    position: absolute;
    left: 0;
    top: 9px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 13px;
    line-height: 19px;
  }
}

.bg-center {
  background-position: center;
}

.lk-nav {
  background: #F6F6F6;
  padding: 20px 0;
  max-width: 100%;
  overflow: auto;

}

.lk-nav-list {
  padding: 0;
  margin: 0;
  display: flex;
  list-style: none;
}

.lk-nav-list__elem a.active {
  color: #00ADEF;

  &:hover {
    color: #4687E0;
  }
}

.lk-nav-list__elem a {
  font-size: 15px;
  line-height: 23px;
  color: #444444;
  text-decoration: unset;
  margin-right: 35px;
  white-space: nowrap;

  &:hover {
    color: #00ADEF;
  }

  @include media-breakpoint-down(sm) {
    font-size: 13px;
    line-height: 19px;
  }
}

.assistive-text.assistive-text_right {
  width: 100%;
  text-align: right;
  font-size: 15px;
  line-height: 18px;
  margin-top: -5px;
  font-weight: 300;

  .code-disable-timer {
    color: #888888;
  }
}

.assistive-text.assistive-text_right.disabled {

  .recive-code-again {
    color: #DADADA;
    cursor: default;
    pointer-events: none;
  }
}