.s-p-wrp {
  display: contents; }
  .s-p-wrp.slick-slide {
    height: auto; }

.service-popup-mobile-navigation {
  display: none;
  margin-bottom: 30px;
  overflow: hidden;
  width: 100%;
  height: 4px; }
  @media (max-width: 767.98px) {
    .service-popup-mobile-navigation {
      display: block; } }

.service-popup-mobile-navigation-item {
  height: 4px;
  width: calc((100vw - 30px) / 3);
  background-color: #f6f6f6;
  transition: background-color 0.25s ease; }

.slick-current.service-popup-mobile-navigation-item {
  background-color: #dadada; }
