.tabs-container {
  margin-top: 50px;

  @include media-breakpoint-down(sm) {
    margin-top: 25px;
  }
}
.tabs-control-box {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  @include media-breakpoint-down(sm) {
    justify-content: center;
  }
}

.tab-item {
  padding: 15px;
  margin-right: 15px;
  line-height: 23px;
  transition: color 0.25s ease;
  color: $minor;
  background-color: transparent;
  border-radius: 0;
  border: none;
  height: auto;
  font-family: inherit;

  &:hover {
    background-color: transparent;
    color: #00adef;
    cursor: pointer;
  }

  &:last-child {
    margin-right: 0;
  }

  @include media-breakpoint-down(sm) {
    flex-grow: 1;
    flex-shrink: 0;
    width: calc(50% - 15px);
    font-size: 13px;
    line-height: 19px;
  }

  @media(max-width: 767px) {
    margin-right: 0;
    margin-left: 0 !important;
    width: 100%;

    &:first-child {
      margin-bottom: 0;
    }
  }
}

.tab-item.active {
  color: $primary;
  border-bottom: 1px solid $primary;
}

.tabs-content {
  margin-top: 50px;

  @include media-breakpoint-down(sm) {
    margin-top: 25px;
  }
}
